"use server"

import { db } from "@/lib/db"
import { hashPassword, comparePassword, setAuthCookie, clearAuthCookie, getAuthUser } from "@/lib/auth-utils"
import { signToken } from "@/lib/jwt"
import { revalidatePath } from "next/cache"
import { redirect } from "next/navigation"
import { resend } from "@/lib/resend"
import { headers } from "next/headers"

export async function signIn(email: string, password: string) {
  try {
    const user = await db.user.findUnique({
      where: { email },
      include: { profile: true },
    })

    if (!user) {
      return { error: "–ù–µ–≤–µ—Ä–Ω—ã–π email –∏–ª–∏ –ø–∞—Ä–æ–ª—å" }
    }

    const isValid = await comparePassword(password, user.password)
    if (!isValid) {
      return { error: "–ù–µ–≤–µ—Ä–Ω—ã–π email –∏–ª–∏ –ø–∞—Ä–æ–ª—å" }
    }

    const token = await signToken({
      userId: user.id,
      email: user.email,
      role: user.role,
    })

    await setAuthCookie(token)

    // Log login
    const headersList = await headers()
    await db.log.create({
      data: {
        userId: user.id,
        action: "login",
        details: "User logged in",
        ip: headersList.get("x-forwarded-for") || headersList.get("x-real-ip") || "unknown",
        userAgent: headersList.get("user-agent") || "unknown"
      }
    })

    return { success: true }
  } catch (error) {
    console.error("[v0] Sign in error:", error)
    return { error: "–û—à–∏–±–∫–∞ –≤—Ö–æ–¥–∞" }
  }
}

export async function signUp(email: string, password: string, name?: string, referralCode?: string) {
  try {
    const existingUser = await db.user.findUnique({
      where: { email },
    })

    if (existingUser) {
      return { error: "–ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å —Å —Ç–∞–∫–∏–º email —É–∂–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç" }
    }

    const hashedPassword = await hashPassword(password)
    
    // –ì–µ–Ω–µ—Ä–∏—Ä—É–µ–º —É–Ω–∏–∫–∞–ª—å–Ω—ã–π —Ä–µ—Ñ–µ—Ä–∞–ª—å–Ω—ã–π –∫–æ–¥
    const userReferralCode = Math.random().toString(36).substring(2, 10).toUpperCase()
    
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º —Ä–µ—Ñ–µ—Ä–∞–ª—å–Ω—ã–π –∫–æ–¥ –µ—Å–ª–∏ —É–∫–∞–∑–∞–Ω
    let referrerId: string | undefined
    if (referralCode) {
      const referrer = await db.user.findUnique({
        where: { referralCode },
      })
      if (referrer) {
        referrerId = referrer.id
      }
    }

    const user = await db.user.create({
      data: {
        email,
        password: hashedPassword,
        name,
        referralCode: userReferralCode,
        referredBy: referrerId,
        profile: {
          create: {
            balance: 0,
          },
        },
      },
    })

    // –°–æ–∑–¥–∞–µ–º –∑–∞–ø–∏—Å—å –æ —Ä–µ—Ñ–µ—Ä–∞–ª–µ –µ—Å–ª–∏ –µ—Å—Ç—å —Ä–µ—Ñ–µ—Ä–µ—Ä
    if (referrerId) {
      await db.referral.create({
        data: {
          referrerId,
          referredId: user.id,
          earnings: 0,
        },
      })
    }

    const token = await signToken({
      userId: user.id,
      email: user.email,
      role: user.role,
    })

    await setAuthCookie(token)

    // Log registration
    const headersList = await headers()
    await db.log.create({
      data: {
        userId: user.id,
        action: "register",
        details: `User registered with email ${user.email}`,
        ip: headersList.get("x-forwarded-for") || headersList.get("x-real-ip") || "unknown",
        userAgent: headersList.get("user-agent") || "unknown"
      }
    })

    // Send confirmation email
    try {
      await resend.emails.send({
        from: "noreply@vds-hub.com",
        to: user.email,
        subject: "Welcome to VDS Hub!",
        html: `
          <h1>Welcome to VDS Hub, ${user.name || 'User'}!</h1>
          <p>Thank you for registering. Your account has been created successfully.</p>
          <p>You can now log in and start using our VDS services.</p>
          <br>
          <p>Best regards,<br>VDS Hub Team</p>
        `
      })
    } catch (emailError) {
      console.error("Failed to send confirmation email:", emailError)
      // Don't fail registration if email fails
    }

    return { success: true }
  } catch (error) {
    console.error("[v0] Sign up error:", error)
    return { error: "–û—à–∏–±–∫–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏" }
  }
}

export async function signOut() {
  await clearAuthCookie()
  revalidatePath("/", "layout")
  redirect("/")
}

export async function getUser() {
  const authUser = await getAuthUser()
  if (!authUser) return null

  const user = await db.user.findUnique({
    where: { id: authUser.userId },
    include: { profile: true },
  })

  return user
}

export async function getUserProfile() {
  const authUser = await getAuthUser()
  if (!authUser) return null

  const profile = await db.profile.findUnique({
    where: { userId: authUser.userId },
  })

  return profile
}
